export default {
  props: {
    formDefinition: {
      type: Object,
      required: true
    },
    data: {
      type: Object,
      required: true
    },
    borderTop: {
      type: Boolean,
      required: false,
      default: true
    },
    borderBottom: {
      type: Boolean,
      required: false,
      default: true
    }
  },
  components: {
    MpvuePicker
  },

  data() {
    return {
      name: '',
      picker: {},
      originData: this.$lodash.extend({}, this.data)
    };
  },

  methods: {
    handleSelect(name, picker) {
      this.picker = picker;
      this.name = name;
      this.$refs.mpvuePicker.show();
    },

    handlePickerConfirm({
      label,
      value
    }) {
      this.data[this.name] = {
        label,
        value
      };
    },

    handlePickerCancel(cur) {
      this.$emit('pickerCancel', {
        name: this.name,
        ...cur
      });
    },

    handlePickerChange(cur) {
      this.$emit('pickerChange', {
        name: this.name,
        ...cur
      });
    },

    handlePickDate(name) {
      this.name = name;
    },

    handleDateSelect({
      detail: {
        value
      }
    }) {
      this.data[this.name] = value;
    },

    formSubmit({
      detail: {
        value
      }
    }) {
      this.$emit('submit', this.data);
    },

    formReset() {
      this.$emit('reset');
    }

  }
};